  .fb-card {
  /* background-color:white; */
  width:502px;
  border: 1px solid #f30606;
  border-radius:3px;
  margin: auto;
  margin-bottom:10px;
  padding: 12px;
  padding-bottom: 5px; 
  
  box-shadow: 0 0 5px rgba(245, 115, 115, 0.3);
  }
  
  .header {
  margin-bottom:17px;
  }
  
  .co-logo {
  display:block;
  float:left;
  margin-right:8px;
  width:40px;
  height:40px;
  }
  
  .co-name > a {
  font-family: Helvetica;
  font-size:14px;
  font-weight: bold;
  line-height: 1.38;
  color: #365899;
  text-decoration:none;
  margin-bottom:2px;
  }
  
  .co-name > a:hover {
  text-decoration:underline;
  }
  
  .time {
  font-family: Helvetica;
  font-size:12px;
  color: #000000;
  }
  
  .time > a {
  color: #000e29;
  text-decoration:none;
  }
  
  .time > a:hover {
  text-decoration:underline;
  }
  
  .options {
  font-family: Helvetica;
  font-size:12px;
  color: #e5e5e5;
  float:right;
  }
  
  .options:hover {
  color: black; 
  color: rgba(0, 0, 0, .30);
  }
  
  .content {
  clear:both;
  font-family: Helvetica, sans-serif;
  font-size:14px;
  line-height: 1.38;
  }
  
  .reference {
  width:476px;
  }
  .reference-thumb {
  display:block;
  width:500px;
  height:400px;
  }
   
.fb-logo {

  border-radius: 100%;
  display:block;
  width:50px;
  height:50px;
  
} 




